<template lang="pug">
    .file-upload Upload

</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    import Dropzone from 'dropzone'

    export default {
        name: 'we-upload',
        props: ['data'],
        components:{
            'we-editable': WeEditable
        },
        data () {
            return {
                // files: [],
                myDropzone: null
            }
        },
        created(){
            
        },
        mounted(){
            this.upload();
        },
        watch: {
            // files: {
            //     // handler(old, val){
            //     //     this.$emit('upload', val);
            //     // },
            //     deep: true
            // }
        },
        methods: {
            upload(){
                this.myDropzone = new Dropzone(".file-upload", {
                    url: 'https://appdividend.com/',
                    previewsContainer: false,
                    accept: (file, done) => {
                        this.data.push(file);
                        // console.log(this.data);
                    }
                });

            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/Upload.styl"></style>


