<template lang="pug">
    .created-box
        we-editable.title(
            ref="title",
            :content="data.title",
            @update="data.title = $event", 
            placeholder="O que  pra fazer?"
        )
        we-editable.descrition(
            ref="description",
            :content="data.description",
            @update="data.description = $event", 
            placeholder="Descrio.."
        )

        .inserts
            component(v-for="(list, index) in componentsInsert.lists", :is="list", :key="'list'+index", :index="index", @returnLists="getLists")
            component(v-for="(checklist, index) in componentsInsert.checklist", :is="checklist", :key="'checklist'+index", :index="index", @returnCheckLists="getCheckLists")

        .files-uploads(v-if="inserts.files.length")
            .header-uploads
                h3.title Arquivos anexados

            .list-files
                .file(v-for="file in inserts.files")
                    img.image(:src="file.dataURL")

                    .details
                        span.filename {{file.name}}
                        span.size {{file.size}}

        .buttons
            button.button-list(@click="addList") Lista
            button.button-checklist(@click="addCheckList") Lista
            //- button.button-list(@click="selectToll('list')") Lista
            //- button.button-checklist(@click="selectToll('checklist')") Lista
            we-upload(:data="inserts.files")

</template>

<script>
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeChecklist from '@/components/tools-created/We-CheckList.vue'
    import WeList from '@/components/tools-created/We-List.vue'
    import WeUpload from '@/components/tools-created/We-Upload.vue'
    import eventHub from '@/EventHub.js'

    export default {
        name: 'w-createdBox',
        props: ['data'],
        components: {
            'we-editable': WeEditable,
            'we-checklist': WeChecklist,
            'we-list': WeList,
            'we-upload': WeUpload
        },
        data () {
            return {
                componentsInsert: {
                    lists: [],
                    checklist: []
                },
                inserts: {
                    lists: [],
                    checklist: [],
                    files: []
                }
            }
        },
        watch: {
            inserts: {
                handler(old, val){
                    this.$emit('returnInserts', this.inserts);
                },
                deep: true
            }
        },
        mounted(){
            this.$emit('returnInserts', this.inserts);

            eventHub.$on('clearCreatedBox', () => {
                this.clearCreatedBox();
            })
        },
        methods: {
            clearCreatedBox(){
                let inputs = document.querySelectorAll('.created-box .editable');

                for (var i = 0; i < inputs.length; i++) {
                    inputs[i].innerHTML = '';
                }

                this.componentsInsert.lists = [];
                this.componentsInsert.checklist = [];

                this.inserts = {
                    lists: [],
                    checklist: [],
                    files: []
                }
            },
            // getUploads(files){
            //     this.data.files = files;
            // },
            getLists(list, index){
                this.inserts.lists[index] = list;
            },
            getCheckLists(checklist, index){
                this.inserts.checklist[index] = checklist;
            },
            addList(){
                this.componentsInsert.lists.push('we-list');
            },
            addCheckList(){
                this.componentsInsert.checklist.push('we-checklist');
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/CreatedBox.styl"></style>


