<template lang="pug">
    .wrapper-inserts
        .files-uploads(v-if="data.content.files.length")
            .header-uploads
                h3.title Arquivos anexados

            .list-files
                .file(v-for="file in data.content.files")
                    img.image(:src="file.dataURL")
                    .details
                        span.filename {{file.name}}
                        span.size {{file.size}}

                    button.button-delete(@click="deleteImage(file)") Remover

        .list(v-if="data.content.list.length", v-for="(list, index) in data.content.list")
            w-popupConfirm(
                :ref="'popupConfirmList'+index",
                @confirm="deleteList(list, index)",
                @close="closeConfirmDeleteList(index)",
                description="Essa lista sera removida"
            )

            .header
                we-editable.title-list(
                    placeholder="Digite o titulo da lista",
                    :content="list.title",
                    @update="list.title = $event"
                )

                button.button-deleteList(@click="showConfirmDeleteList(index)") Remover lista

            .items
                .item(v-for="item in list.items")
                    we-editable.text(
                        :content="item.text",
                        @update="item.text = $event"
                    )
                    button.button-delete(@click="deleteListItem(item, list)") Remover

            we-editable(
                :ref="'addItemList'+index",
                class="addItem",
                @keyup.enter.native="onCreatedItemList(list, index)",
                :content="contentList.text",
                @update="contentList.text = $event", 
                placeholder="Digitar novo item"
            )

        .checklist(v-if="data.content.checklist.length", v-for="(checklist, index) in data.content.checklist")
            w-popupConfirm(
                :ref="'popupConfirmChecklist'+index",
                @confirm="deleteCheckList(checklist, index)",
                @close="closeConfirmDeleteCheckList(index)",
                description="Essa checklist sera removida"
            )

            .header
                we-editable.title-list(
                    placeholder="Digite o titulo do checklist",
                    :content="checklist.title",
                    @update="checklist.title = $event"
                )

                button.button-deleteCheckList(@click="showConfirmDeleteCheckList(index)") Remover lista

            .items
                .item(v-for="item in checklist.items", :class="item.check ? 'check' : ''") 
                    button.button-check(@click="checkItem(item)")
                    
                    we-editable.text(
                        :content="item.text",
                        @update="item.text = $event"
                    )
                    
                    button.button-delete(@click="deleteCheckItem(item, checklist)") Remover

                we-editable(
                    :ref="'addItemChecklist'+index",
                    class="addItem",
                    @keyup.enter.native="onCreatedItemCheckList(checklist, index)",
                    :content="contentCheckList.text",
                    @update="contentCheckList.text = $event", 
                    placeholder="Digitar novo item"
                )

</template>

<script>
    import WeEditable from '@/components/common/We-Editable.vue'
    import WePopupConfirm from '@/components/common/We-PopupConfirm.vue'
    import eventHub from '@/EventHub'

    export default {
        name: 'w-nav',
        components: {
            'we-editable': WeEditable,
            'w-popupConfirm': WePopupConfirm
        },
        props: ['data'],
        data () {
            return {
                isConfirmDeleteList: false,
                isConfirmDeleteCheckList: false,
                contentList: {
                    text: ''
                },
                contentCheckList: {
                    text: ''
                },
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){

        },
        methods: {
            showConfirmDeleteList(index){
                this.$refs['popupConfirmList'+index][0].$el.classList.add('show');
            },
            showConfirmDeleteCheckList(index){
                this.$refs['popupConfirmChecklist'+index][0].$el.classList.add('show');
            },
            closeConfirmDeleteCheckList(index){
                this.$refs['popupConfirmChecklist'+index][0].$el.classList.remove('show');
            },
            closeConfirmDeleteList(index){
                this.$refs['popupConfirmList'+index][0].$el.classList.remove('show');
            },
            deleteList(list, index){
                var index = this.data.content.list.indexOf(list);
                if (index > -1) {
                    this.data.content.list.splice(index, 1);
                }

                this.closeConfirmDeleteList(index);
            },
            deleteCheckList(checklist){
                var index = this.data.content.checklist.indexOf(checklist);
                if (index > -1) {
                    this.data.content.checklist.splice(index, 1);
                }

                this.isConfirmDeleteCheckList = false;
            },
            checkItem(item){
                if (item.check) {
                    item.check = false;
                }else{
                    item.check = true
                }
            },
            deleteListItem(item, list){
                var index = list.items.indexOf(item);

                if (index > -1) {
                    list.items.splice(index, 1);
                }
            },
            deleteCheckItem(item, checklist){
                var index = checklist.items.indexOf(item);

                if (index > -1) {
                    checklist.items.splice(index, 1);
                }
            },
            onCreatedItemList(list, index){
                let item = {
                    text: this.contentList.text
                }

                list.items.push(item);
                this.$refs['addItemList'+index][0].$el.innerHTML = '';
            },
            onCreatedItemCheckList(checklist, index){
                let item = {
                    text: this.contentCheckList.text,
                    check: false
                }

                checklist.items.push(item);
                this.$refs['addItemChecklist'+index][0].$el.innerHTML = '';
            },
            deleteImage(file){
                var index = this.data.content.files.indexOf(file);
                if (index > -1) {
                    this.data.content.files.splice(index, 1);
                }
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Inserts.styl"></style>


