<template lang="pug">
	.boards
		w-popupConfirm(
		    @confirm="confirmDeleteBoard",
		    @close="cancelClose",
		    v-if="isConfirmClose", 
		    description="Seu quadro perdera todas suas tarefas se for excluido"
		)

		.header-boards
			.group-user
				.user-content
					.photo(:style="{'background-image':'url('+masteruser.infos.photo+')'}")
					.welcome
						h2.title Bom dia {{masteruser.infos.name}}.
						.count-tasks
							span.tasks 2 tarefas
							span.masrcacoes 3 marcaes

				.notifications
					.icon-count
						span 2
					.content
						.icon
						p.text Voc tem 2 compromissos por hoje

			.group-buttons
				button.button-created(@click="openCreated") Criar Tarefa
				button.search

		.wrapper-board(ref="wrapperboard")
			.header-board
				.wrap-title
					h2.title {{currentBoards.title}}
					.lines
						.line(:style="{'background-color' : currentBoards.color}")
						.line(:style="{'background-color' : currentBoards.color}")

				button.toggle(@click="showBoards") {{this.isActive ? 'Fechar' : 'Alterar quadro'}}

			.list-boards
				.wrapper-listboards
					.board(:ref="'board'+board.id", :style="{'background-color' : board.color}", v-for="board in boards", :class="board.id == currentBoards.id ? 'active' : ''", @click="goBoard(board.id)")
						.header
							.icon-active(:style="{'background-color' : board.color}")
							.options(:ref="'option'+board.id")
								button.button-options(@click.stop="openOption(board.id)")
									.dot
									.dot
									.dot

								button.button-closeOption(@click.stop="closeOption(board.id)")

								.list(@click.stop="stop")
									button.button-editar(@click.stop="openEditBoard(board.id, board)")
										span.text Editar
										.wrapper-edit
											.content-text
												we-editable.title(
													ref="editable",
													:maxLenght="22",
													:content="contentBoardEdit.title",
													@update="contentBoardEdit.title = $event", 
													placeholder="Digite o titulo"
												)

												.colors
													button.button-color(
														@click="setColorEdit($event.target)", 
														v-for="(color, index) in colors", :data-color="color.code", 
														:data-active="board.color === color.code",
														:ref="'color-'+index", 
														:style="{'background-color' : color.code}")

												.buttons
													button.button-created(@click.stop="saveEditBoard(board)") Salvar
													button.button-cancel(@click.stop="closeEditBoard(board)") Cancelar

									button.button-remove(
										@click.stop="removeBoard(board)", 
										:class="boards.length <= 1 ? 'disable' : ''") Remover
						.content
							h3.title {{board.title}}
							span.count-tasks 32 tasks

					.create-board(ref="createBoard", @click="openCreatedBoard")
						.content-text
							we-editable.title(
								ref="editable",
								:maxLenght="22",
								:content="contentBoard.title",
								@update="contentBoard.title = $event", 
								placeholder="Digite o titulo"
							)
						
						.colors
							button.button-color(@click="setColor($event.target)", v-for="(color, index) in colors", :data-color="color.code", :ref="'color-'+index", :style="{'background-color' : color.code}")

						.buttons
							button.button-created(@click.stop="createdBoard") Criar
							button.button-cancel(@click.stop="closeCreatedBoard") Cancelar

						h4.title Criar Quadro

			we-colsboard(:cols="currentBoards.cols", :idBoard="currentBoards.id")
		
		.wrapper-arquivados(:style="{'background-color' : currentBoards.color}")
			.header-arquivados
				h3.title Concluidas do quadro - {{currentBoards.title}}
				span.description Arraste para c suas tarefas concluidas
			
			draggable(
				class="list",
				v-model="currentBoards.arquivados",
				:options="{group:'cards', handle: '.header-arquivados', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}"
			)
				we-task(v-for="(task, index) in currentBoards.arquivados", :idBoard="currentBoards.id", :idCol="getIdCol(task.id)", :key="index", :ref="task.id", :data="task")
			
		router-view(:key="$route.fullPath")
</template>

<script>
	import Boards from '@/components/boards/boards.js'
	import WeColsBoard from '@/components/boards/We-ColsBoard.vue';
	import draggable from 'vuedraggable'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeTask from '@/components/boards/We-Task.vue'
	import MasterUser from '@/master-user.js'

	import WePopupConfirm from '@/components/common/We-PopupConfirm.vue'

	export default {
		name: 'we-boards',
		components:{
			'w-popupConfirm': WePopupConfirm,
			'we-task': WeTask,
			'we-editable': WeEditable,
			'we-colsboard': WeColsBoard,
			'draggable': draggable
		},
		props: ['data'],
		data () {
			return {
				masteruser: MasterUser,
				isConfirmClose: false,
				boardTempDelete: null,
				contentBoardEdit: {
					color: null,
					title: null
				},
				colors:[
					{
						code:'#290062'
					},
					{
						code:'#756AFF'
					},
					{
						code:'#F68381'
					},
					{
						code:'#F9AB00'
					},
					{
						code:'#83DEF3'
					}
				],
				isActive: false,
				boards: Boards.boards,
				contentBoard: {
					color: '#290062',
					title: null
				},
				currentBoards: null
			}
		},
		created(){
			for (var i = 0; i < this.boards.length; i++) {
				if (this.boards[i].id == this.$route.params.id) {
					this.currentBoards = this.boards[i];
				}
			}

			this.contentBoardEdit.title = this.currentBoards.title;
		},
		mounted(){
			document.querySelector('.button-color').setAttribute('data-active', true);
		},
		methods:{
			stop(){},
			cancelClose(){
				this.isConfirmClose = false;
			},
			removeBoard(board){
				this.isConfirmClose = true;
				this.boardTempDelete = board;
			},
			confirmDeleteBoard(){
				var index = this.boards.indexOf(this.boardTempDelete);
				if (index > -1) {
					this.boards.splice(index, 1);
				}

				this.$router.push({
					name: 'boards', 
					params: {
						id: this.boards[0].id
					}
				})
			},
			saveEditBoard(board){
				board.title = this.contentBoardEdit.title;

				if (this.contentBoardEdit.color) {
					board.color = this.contentBoardEdit.color;
				}

				this.closeOption(board.id)
			},
			setColorEdit(el){
				let elsColors = document.querySelectorAll('.button-color');
				this.contentBoardEdit.color = el.getAttribute('data-color');

				for (var i = 0; i < elsColors.length; i++) {
					elsColors[i].removeAttribute('data-active');
				}

				el.setAttribute('data-active', true);
			},
			openEditBoard(id, board){
				this.$refs['board'+id][0].classList.add('open-edit');
				// this.contentBoardEdit.title = 'asd  asd';
			},
			openOption(id){
				this.$refs['option'+id][0].classList.add('open');
			},
			closeEditBoard(board){
				this.$refs['board'+board.id][0].classList.remove('open-edit');
			},
			closeOption(id){
				this.$refs['option'+id][0].classList.remove('open');
				this.$refs['board'+id][0].classList.remove('open-edit');
			},
			openCreatedBoard(){
				this.$refs.createBoard.classList.add('add-board');
			},
			closeCreatedBoard(){
				this.$refs.createBoard.classList.remove('add-board');
			},
			getIdCol(idTask){
				for (var i = 0; i < this.currentBoards.cols.length; i++) {
					return this.currentBoards.cols[i].id;
				}
			},
			createdBoard(){
				if (this.contentBoard.title) {
					let lastBoardId = this.boards[this.boards.length-1].id;
					
					let tempObj = {
						id: lastBoardId+1,
						color: this.contentBoard.color,
						title: this.contentBoard.title,
						cols: []
					}

					this.boards.push(tempObj);
					this.closeCreatedBoard();

					this.contentBoard.color = '#290062';
					this.contentBoard.title = null;
					this.$refs.editable.$el.innerHTML = '';
				}
			},
			setColor(el){
				let elsColors = document.querySelectorAll('.button-color');
				this.contentBoard.color = el.getAttribute('data-color');

				for (var i = 0; i < elsColors.length; i++) {
					elsColors[i].removeAttribute('data-active');
				}

				el.setAttribute('data-active', true);
			},
			openCreated(){
				this.$router.push({
					name: 'created'
				})
			},
			goBoard(idBoard){
				this.$router.push({
					name: 'boards',
					params: {
						id: idBoard
					}
				})
			},
			end(item){
				console.log('oi');
			},
			showBoards(){
				this.isActive = !this.isActive;
				this.$refs.wrapperboard.classList.toggle('show-boards')
			},
			getBoards(){

			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/boards/Boards.styl"></style>


